import axios, { Axios, CreateAxiosDefaults } from 'axios'

import { ApiClientModel } from './api-client.model'
import { CampaignsClient } from '../campaigns'
import { SignaturesClient } from '../signatures'
import { TransactionsClient } from '../transactions'
import { UsersClient } from '../users'
import { VaultEpochsClient } from '../vault-epochs'
import { ChainsClient } from '../chains'
import { OperatorsClient } from '../operators'
import { TokensClient } from '../tokens'
import { VaultBlocksClient } from '../vault-blocks'
import { VaultCategoriesClient } from '../vault-categories'
import { VaultLatestBlocksClient } from '../vault-latest-blocks'
import { VaultTypesClient } from '../vault-types'
import { VaultsClient } from '../vaults'
import { WalletLatestBlocksClient } from '../wallet-latest-blocks'
import { WalletsClient } from '../wallets'
import { CampaignPointsClient } from '../campaign-points'
import { WalletPerformanceClient } from '../wallet-performances'
import { WalletBlocksClient } from '../wallet-blocks'
import { VaultRequestsClient } from '../vault-requests'
import { VaultRewardsClient } from '../vault-rewards'

/**
 * API Client class
 */
export class ApiClient implements ApiClientModel {
  // Services
  public campaignPoints
  public campaigns
  public chains
  public operators
  public tokens
  public transactions
  public users
  public vaultBlocks
  public vaultCategories
  public vaultEpochs
  public vaultLatestBlocks
  public vaultRequests
  public vaultRewards
  public vaultTypes
  public vaults
  public walletBlocks
  public walletLatestBlocks
  public walletPerformances
  public wallets
  public signatures

  // Axios instance
  private axios: Axios

  constructor(apiUrl: string, token: string) {
    this.axios = this.initAxios(apiUrl, token)
    this.campaignPoints = new CampaignPointsClient(this.axios)
    this.campaigns = new CampaignsClient(this.axios)
    this.chains = new ChainsClient(this.axios)
    this.operators = new OperatorsClient(this.axios)
    this.signatures = new SignaturesClient(this.axios)
    this.tokens = new TokensClient(this.axios)
    this.transactions = new TransactionsClient(this.axios)
    this.users = new UsersClient(this.axios)
    this.vaultBlocks = new VaultBlocksClient(this.axios)
    this.vaultCategories = new VaultCategoriesClient(this.axios)
    this.vaultEpochs = new VaultEpochsClient(this.axios)
    this.vaultLatestBlocks = new VaultLatestBlocksClient(this.axios)
    this.vaultRequests = new VaultRequestsClient(this.axios)
    this.vaultRewards = new VaultRewardsClient(this.axios)
    this.vaultTypes = new VaultTypesClient(this.axios)
    this.vaults = new VaultsClient(this.axios)
    this.walletBlocks = new WalletBlocksClient(this.axios)
    this.walletLatestBlocks = new WalletLatestBlocksClient(this.axios)
    this.walletPerformances = new WalletPerformanceClient(this.axios)
    this.wallets = new WalletsClient(this.axios)
  }

  /**
   * Axios initialization
   */
  private initAxios(apiUrl: string, token: string) {
    const options: CreateAxiosDefaults = {
      baseURL: `${apiUrl}`,
      headers: {
        Authorization: `Bearer ${token}`,
        'content-type': 'application/json',
      },
    }
    return axios.create(options)
  }
}
